<template>
  <div>
    <el-button type="primary" @click="onOpen">打开 Dialog</el-button>
    <DialogByCustom :visible.sync="dialogVisible" />
    <el-input type="textarea" v-model="sourceCode" rows="10" />
    <div v-html="code"></div>
    <el-input type="textarea" v-model="inputText" rows="10" />
  </div>
</template>

<script>
import DialogByCustom from './components/DialogByCustom'

import hljs from 'highlight.js/lib/core'
import javascript from 'highlight.js/lib/languages/javascript'
import 'highlight.js/styles/atom-one-dark.css'

hljs.registerLanguage('javascript', javascript)

export default {
  name: 'ListAndDialog',
  components: {
    DialogByCustom,
  },
  data() {
    return {
      dialogVisible: false,
      inputText: ``,
      sourceCode: `console.log('123')`,
      code: ``,
    }
  },
  watch: {
    sourceCode(val) {
      const highlightedCode = hljs.highlight(this.sourceCode, { language: 'javascript' }).value

      this.code = highlightedCode
    },
  },
  created() {
    this.inputText = this.readFile('../../../static/code/DialogByCustom.vue')
  },
  mounted() {
    console.log(DialogByCustom)

    const highlightedCode = hljs.highlight(this.sourceCode, { language: 'javascript' }).value

    this.code = highlightedCode
  },
  methods: {
    onOpen() {
      this.dialogVisible = true
    },
    readFile(filePath) {
      let xhr = null
      if (window.XMLHttpRequest) {
        xhr = new XMLHttpRequest()
      } else {
        xhr = new ActiveXObject('Microsoft.XMLHTTP')
      }
      const okStatus = document.location.protocol === 'file' ? 0 : 200
      xhr.open('GET', filePath, false)
      xhr.overrideMimeType('text/html;charset=utf-8')
      xhr.send(null)
      return xhr.status === okStatus ? xhr.responseText : null
    },
  },
}
</script>
